  margin-top: -13vh;
  transform: skewY(-7deg);

  & > * {
    transform: skewY(7deg);
  }
  //clip-path: polygon(0 10%, 100% 0 , 100% 80%, 0 100%);
}

.section-features {
  //background-color: $color-grey-light-1;
  padding: 10rem 0 20rem 0;
  background-image: linear-gradient(
                  to right bottom,
                  rgba($color-primary-light, .2),
                  rgba($color-primary-dark, .2));
  margin-top: 0rem;

  //background-attachment: fixed;
  //background-size: cover;
  //background-position: top;
  //background-repeat: no-repeat;

  //clip-path: polygon(0 20%, 100% 0 , 100% 80%, 0 100%);


  transform: skewY(-7deg);
  & > * {
   transform: skewY(7deg);
 }

}

.section-method-alt {
  background-color: $color-grey-light-1;
  margin-top: -10rem;
  padding: 15rem 0 15rem 0;

 // clip-path: polygon(0 20%, 100% 0 , 100% 80%, 0 100%);
  transform: skewY(-7deg);

  & > * {
    transform: skewY(7deg);
  }
}
.method-alt {
  // border: 1px red solid;
  background-color: $color-grey-light-1;
  //  padding: 30rem 0;
  text-align: center;
  display: block;
  margin: auto;
  //margin-top: -20vh;
  width: 65%;

  &__photo {


    font-size: 1.5rem;

    .img {
      margin-top: -8rem;
      border-radius: 100%;
      width: 35%;
    }
  }

  &__text {
    color: black;
    overflow-wrap: break-word;
    padding-top: 4rem;
    font-size: 2rem;
  }


}

.section-about-list {

  padding: 15rem 0 15rem 0;
  //margin-top: -10rem;

  transform: skewY(-7deg);
  & > * {
    transform: skewY(7deg);
  }

  background-image: linear-gradient(
                  to right bottom,
                  rgba($color-primary-light, 0.2),
                  rgba($color-primary-dark, 0.2));


  ul {
    font-size: 1.5rem;
    list-style-type: none;
    margin-top: 1rem;
  }

  li {
    text-align: center;
    //line-height: 3rem;
  }

  hr {
    width: 60%;
    margin: auto;
  }

  .portrait {

    &__img {
      display: block;
      width: 50%;
      border-radius: 2%;
      margin: auto;
      margin-left: 6rem;
      transition: all .5s;
      box-shadow: 0 1.5rem 4rem rgb($color-black, .4);

      &:hover{


        box-shadow:
                4px 4px #373737,
                5px 5px #373737,
                6px 6px #373737,
                7px 7px #373737,
                8px 8px #373737,
                9px 9px #373737;
        -webkit-transform: translateX(-3px);
        transform: translateX(-3px);
        transition: .5s ease;


        //outline: 1.5rem solid $color-primary;
        //  transform: scale(1.05) translateY(-.5rem);
        // box-shadow: 0 2.5rem 4rem rgb($color-black, .5);

      }


    }

    // margin-left: 5rem;
  }


  .about-list__content{
    display: block;
    margin-left: 10rem;
  }
}

.section-contact {
  padding: 30rem 0 20rem 0;
  background-color: $color-grey-light-1;
  margin-top: -15rem;


  clip-path: polygon(0 20%, 100% 0 , 100% 100%, 0 100%);

 // transform: skewY(-7deg);

 // & > * {
 //   transform: skewY(7deg);
 // }
}



