body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.7;
  color: #777;
}

.heading-primary {
  color: $color-button-white;
  text-transform: uppercase;
  backface-visibility: hidden;


  /*&--main {
    display: block;
    letter-spacing: 2.4rem;
    font-size: 5rem;
    font-weight: 700;


      display: block;
    letter-spacing: 3rem;
    font-size: 6rem;
    font-weight: 500;

*/
  &--main {

    display: block;
    letter-spacing: 2.4rem;
    font-size: 5rem;
    font-weight: 700;


    animation: moveInLeft 1s ease-out;

    /*
  animation-name:  moveInLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out;


    animation-iteration-count: 5;
    animation-delay: 3s;
    */
  }

  /*&--sub {
    display: block;
    font-size: $default-font-size;
    font-weight: 400;
    letter-spacing: 1.34rem;

    animation: moveInRight 1s ease-out;
  }*/

  &--sub {
    display: block;
    font-size: 2rem;
    font-weight: 400;
    letter-spacing: 1.34rem;

    animation: moveInRight 1s ease-out;
  }
}


.heading-secondary {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: .2rem;

  background-image: linear-gradient(
                  to right,
                          ($color-primary-light,
                          $color-primary-dark));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  transition:  all 0.2s;

 // &:hover{
//transform: skewY(2deg) skewX(10deg) scale(1.1);
//    text-shadow: .5rem 1rem 2rem rgb($color-black, 0.2);
//  }
}

.heading-tertiary{
  font-size: $default-font-size;
  font-weight: 700;
  text-transform: uppercase;


}

.paragraph{
  font-size: $default-font-size;


  &:not(last-chid){
    margin-bottom: 6rem ;
  }
}